.label {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    padding: 20px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: bold;
    color: rgb(92, 92, 92);
    box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2);
  }
  
  .input {
    padding: 10px;
    margin-top: 16px;
    border: 1px solid rgb(165, 165, 165);
    border-radius: 4px;
    outline: none;
    transition: border-color 0.3s;
    color: rgb(145, 145, 145);
    font-size: 20px;
  }
  .input:focus {
    border-color: rgb(59, 172, 48);
    box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2);
  }